colors
========================================================= */
@import "colors";

/* =========================================================
header
========================================================= */
.header {
  &__title {
    text-align: center;
    img {
      vertical-align: bottom;
    }
  }
}
@media screen and (min-width:769px) {
  .header {
    &__title {
      img {
        width: auto;
        max-width: 100%;
      }
    }
  }
}


