/* =========================================================
ヘッダー下バナー
========================================================= */
.top-bnr-block {
  max-width: 596px;
  margin: 0 auto;
  padding: 20px 0 0;
  .bnr {
    margin: 0 auto 5px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}
@media screen and (min-width:769px) {
  .top-bnr-block {
    max-width: 930px;
    padding: 60px 0 0;
    .bnr {
      margin: 0 auto 20px;
    }
  }
}



/* =========================================================
YouTube
========================================================= */
.youtube {
  &-block {
    max-width: 1024px;
    margin: 0 auto;
    padding: 20px 0 0;
  }
  &-wrap {
    padding-top: 56.25%;
    position: relative;
    iframe {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}
@media screen and (min-width:769px) {
  .youtube {
    &-block {
      padding: 60px 0 0;
    }
  }
}




/* =========================================================
Local navigation
========================================================= */
.localnav-wrap {
  max-width: 1024px;
  padding: 30px 0 0;
  margin: 0 auto;
}
.localnav {
  padding: 0 10px;
  display: flex;
  justify-content: center;
  &__item {
    width: calc(50% - 5px);
    position: relative;
    margin: 0 10px 0 0;
    &:last-child {
      margin-right: 0;
    }
    a {
      display: block;
      padding: 7px 0;
      text-align: center;
      background: $localnav;
      font-size: 1.6rem;
      font-weight: bold;
      position: relative;
      color: $localnav_txt;
      border-radius: 5px;
      text-decoration: none;
      &:after {
        content: "";
        display: inline-block;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 8px 8px 0 8px;
        border-color: transparent transparent transparent transparent;
        position: absolute;
        bottom: -8px;
        left: 50%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
      }
    }
  }
}
@media screen and (min-width:769px){
  .localnav-wrap {
    padding: 60px 0 0;
  }
  .localnav {
    padding: 0;
    &__item {
      width: calc(50% - 20px);
      margin: 0 40px 0 0;
      a {
        padding: 23px 0;
        font-size: 2.1em;
        border-radius: 8px;
        &:after {
          border-width: 8px 6px 0 6px;
        }
        &:hover {
          opacity: 0.7;
          text-decoration: none;
          background: $localnav_on;
          color: $localnav_on_txt;
        }
      }
    }
  }
}

/* current */
body.page-sale .localnav__item01,
body.page-tag .localnav__item02 {
  a {
    background: $localnav_on;
    color: $localnav_on_txt;
    &:after {
      border-color: $localnav_on transparent transparent transparent;
    }
  }
}



/* =========================================================
Contents block
========================================================= */
.contents {
  &-wrap {
    max-width: 1024px;
    margin: 40px auto 0;
  }
  &-block {
    margin: 0 0 40px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &-ttl {
    margin: 0 auto 25px;
    padding: 9px 0;
    background: $title_bk;
    color: $title_txt;
    line-height: 1.1;
    text-align: center;
    font-family: "Roboto Condensed", sans-serif;
    font-size: 2rem;
    letter-spacing: 0.15em;
    position: relative;
  }
}
@media screen and (min-width:769px){
  .contents {
    &-wrap {
      margin: 80px auto 0;
    }
    &-block {
      margin: 0 0 100px;
    }
    &-ttl {
      margin: 0 auto 70px;
      font-size: 3rem;
    }
  }
}




/* =========================================================
タグ集約
========================================================= */
.taglist {
  overflow: hidden;
  &__item {
    border-bottom: 1px solid #ccc;
    margin-bottom: 15px;
    list-style: none;
    display: none;
    &:first-child {
    border-top: 1px solid #ccc;
    }
    &:last-child {
      margin-bottom: 0;
    }
    &:nth-child(-n+6) { /* 初期表示6件 */
      display: block;
    }
    &__in {
      height: 100%;
    }
    a {
      display: block;
      position: relative;
      height: 100%;
      padding: 15px;
      color: #555;
      background: #fff;
      font-size: 1.4rem;
      line-height: 1.5;
      text-decoration: none;
      box-sizing: border-box;
      &:before {
        content: "";
        display: block;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0 0 10px 10px;
        border-color: transparent transparent #555 transparent;
        position: absolute;
        right: 4px;
        bottom: 4px;
      }
    }
  }
  &__txt {
    margin-top: 15px;
  }
  &__photo {
    position: relative;
    padding: 0 40px;
  }
  &__kaeru {
    position: absolute;
    top: 0;
    left: 40px;
    font-size: 10px;
    font-size: 1rem;
    color: #fff;
    line-height: 1.1;
    display: inline-block;
    background: #ea5550;
    padding: 7px 5px;
  }
  &__name {
    font-size: 12px;
    font-size: 1.2rem;
    margin-bottom: 5px;
  }
  &__title {
    font-weight: bold;
    margin-bottom: 6px;
  }
}
@media screen and (min-width:769px) {
  .taglist {
    display: flex;
    flex-wrap: wrap;
    &__item {
      width: calc(33.33% - 10px);
      margin: 15px 15px 0 0;
      border: 1px solid #ccc;
      box-sizing: border-box;
      &:nth-child(-n+3) {
        margin-top: 0;
      }
      &:nth-child(3n) {
        margin-right: 0;
      }
    }
    &__photo {
      padding: 0;
    }
  }
}
.btn-taglist-more {
  display: none;
  width: calc(100% - 150px);
  max-width: 435px;
  margin: 30px auto 0;
  padding: 15px 0;
  background: $more_bk;
  border-radius: 10px;
  border: none;
  color: $more_txt;
  font-size: 2rem;
  font-family: "Roboto Condensed", sans-serif;
  cursor: pointer;
  letter-spacing: 0.15em;
  font-weight: bold;
  @media screen and (min-width:769px) {
    margin: 40px auto 0;
    &:hover {
      opacity: 0.7;
    }
  }
}




