/* 館ナビ */
.floornav {
  width: calc(100% - 40px);
  max-width: 620px;
  margin: 0 auto 20px;
  display: flex;
  justify-content: space-between;
  border: 1px solid #000;
  border-radius: 4px;
  li {
    list-style: none;
    width: 50%;
    margin: 0;
    a {
      display: block;
      width: 100%;
      padding: 3px 0;
      display: inline-block;
      text-align: center;
      color: #000;
      font-size: 1.4rem;
      font-weight: bold;
      position: relative;
      text-decoration: none;
    }
  }
}
@media screen and (min-width:769px){
  .floornav {
    margin: 0 auto 50px;
    li {
      a {
        font-size: 2.0rem;
        text-decoration: none;
        color: #000;
        &:hover {
          text-decoration: none;
          opacity: 0.7;
        }
      }
    }
  }
}

/* current */
body.type1 .floornav .floornav__item01 a,
body.type2 .floornav .floornav__item02 a,
body.type3 .floornav .floornav__item03 a,
body.type4 .floornav .floornav__item04 a {
  background: #000;
  color: #FFF;
}
body.type1 .floornav .floornav__item01 a:after,
body.type2 .floornav .floornav__item02 a:after,
body.type3 .floornav .floornav__item03 a:after,
body.type4 .floornav .floornav__item04 a:after {
  border-color: #000 transparent transparent transparent;
}


/* 値書き*/
.salelist-container {
  margin: 50px 0 0;
}

.salelist-info1, .salelist-info2 {
  font-size: 1.5rem;
  padding: 0 10px;
  line-height: 1.6;
}

.salelists {
  margin: 20px auto 0;
  @media screen and (min-width:769px){
    margin: 60px auto 0;
  }
}
.salelist {
  padding: 0 0 10px;
  &__title {
    display: flex;
    justify-content: space-between;
    align-items:center;
    position: relative;
    padding: 9px 20px;
    background: $floor_bk;
    color: $floor_txt;
    line-height: 1.1;
    .ico-arr {
      text-align: center;
      background: $floor_arrow01;
      width: 18px;
      height: 18px;
      border-radius: 9px;
      display: inline-block;
      position: relative;
      &:after {
        content: '';
        display: inline-block;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 6px 4px 0 4px;
        border-color: $floor_arrow02 transparent transparent transparent;
        position: absolute;
        left: calc(50% - 4px);
        top: calc(50% - 3px);
        transition-duration: 200ms;
      }
    }
    &__floor {
      font-family: "Roboto Condensed", sans-serif;
      font-size: 2rem;
      letter-spacing: 0.15em;
    }
  }
  &__shop {
    font-size: 1.4rem;
    border-top: 1px solid #ccc;
    &:first-child {
      margin-top: 0;
      border-top: none;
    }
  }
  &__link {
    padding: 30px 15px;
    display: block;
    text-decoration: none;
    color: #000;
  }
  &__data {
    display: table;
    width: 100%;
    &__item01 {
      display: table-cell;
      width: 80px;
      padding-right: 15px;
      vertical-align: middle;
      img {
        border: 1px solid #ccc;
      }
    }
    &__item02 {
      display: table-cell;
      vertical-align: middle;
    }
  }
  &__shopname {
    font-size: 1.2rem;
    font-weight: bold;
  }
  &__text {
    margin-top: 5px;
  }
}
@media screen and (min-width:769px){
  .salelist {
    padding: 0 0 40px;
    &__title {
      &__floor {
        font-size: 3rem;
      }
    }
    &__cont {
      &__in{
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
      }
    }
    &__shop {
      width: 50%;
      margin-top: 0;
      padding: 30px 20px;
      box-sizing: border-box;
      &:first-child {
      }
      &:nth-child(-n+2) {
        border: none;
      }
    }
    &__link {
      &:hover {
        opacity: 0.7;
        text-decoration: none;
      }
    }
    &__data {
      &__item01 {
        width: 120px;
        padding-right: 25px;
        img {
          width: 100%;
          border: none;
        }
      }
      &__item02 {
      }
    }
    &__shopname {
      font-size: 1.3rem;
    }
    &__text {
      font-size: 1.5rem;
      margin-top: 10px;
      line-height: 1.6;
    }
  }
}
/* 開閉時の矢印*/
.salelist{
  &__title{
    &.is-open {
      .ico-arr {
        &:after {
          -webkit-transform:rotate(180deg);
                  transform:rotate(180deg)
        }
      }
    }
  }
}


