// coding

body {
  font-family: "Montserrat", sans-serif;
}
.montserrat {
  font-family: "Montserrat", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
}

.zen-kaku-gothic-antique-regular {
  font-family: "Zen Kaku Gothic Antique", sans-serif;
  font-weight: 400;
  font-style: normal;
}
.hidden {
    display: none;
}
.main {
    &.active {
        .padding-main {
            padding-top: 60px;
        }

        .fukuoka-parco {
            display: block;
            overflow: hidden;
        }

        .sns {
            display: block;
        }

        .other-text {
            display: flex;
        }
    }
}


.loading {
    position: relative;
    height: 100vh;
    canvas {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 1;
        @media (max-width: 768px) {
          width: 375px;
          height: 375px;
        }
    }

    &__body {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        // background: url('../images/main-loading.png') no-repeat;
        background-size: 100%;
        transition: filter 0.5s ease;
        video {
            width: 100%;
            height: 100vh;
            object-fit: cover;
        }

        &.blur {
            transition: .3s all;
            filter:blur(10px);
        }
    }

    &__text {
        position: absolute;
        bottom: 0;
        left: 20px;
        bottom: 20px;
        font-size: 1.2rem;
        font-weight: bold;
        display: flex;
        align-items: center;
        color: #fff;
        font-family: "Montserrat", sans-serif;
        font-weight: 900;
        z-index: 2;
        span {
            margin-left: 20px;
        }

        &.hidden {
            display: none;
        }
    }
}

.scroll-main {
    font-size: 1.2rem;
    font-family: "Montserrat", sans-serif;
    font-weight: bold;
    color: #fff;
    position: absolute;
    right: 40px;
    bottom: 90px;
    padding-left: 10px;
    transform: rotate(90deg);

    @media (max-width: 768px) {
      right: -10px;
      bottom: 73px;
    }

    span {
      &:after {
        content: '';
        display: inline-block;
        position: absolute;
        left: 9px;
        bottom: -10px;
        width: 170%;
        height: 1px;
        background: #ececf5;
        animation: arrow 3.0s linear infinite;
      }
    }
}



.main-video {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    //opacity: 0;
    overflow: hidden;
    transition: .3s all;
    video {
        width: 100%;
        height: 100vh;
        object-fit: cover;
    }
}
.next-sound-line {
    position: fixed;
    z-index: 999;
    bottom: 20px;
    left: 20px;

    @media (max-width: 768px) {
      position: fixed;
      left: 10px;
      bottom: 10px;
    }

    &.hidden {
        opacity: 0;
    }

    .ui-sound-toggle {
        color: #fff;
    }
}

.select__sound {
    width: 412px;
    height: 412px;
    border-radius: 100%;
    border: 1px solid #fff;
    z-index: 3;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    justify-content: center;
    @media (max-width: 768px) {
      width: 298px;
      height: 298px;
    }

    &:before {
        position: absolute;
        width: 100%;
        height: 100%;
        background-size: 100%;
        content: "";
        border-radius: 100%;
        z-index: -1;
        backdrop-filter: blur(7px);
    }

    &.hidden {
        opacity: 0;
        z-index: -1;
        transition: 0.5s all;
    }

    .ui-sound-toggle {
        text-align: center;
        transform: translate(0px, -30px);
    }

    .ui-sound-toggle-static, .ui-sound-toggle-hover-wrap {
        display: none;
    }

    .ui-sound-toggle-rects rect {
        fill: #fff;
    }

    &-choice {
        display: flex;
        align-items: center;
        font-size: 1.6rem;
        font-weight: 900;
        font-family: "Montserrat", sans-serif;
        color: #fff;
        @media (max-width: 768px) {
          font-size: 14px;
        }

        &-item {
            padding: 0 31px;
            cursor: pointer;

            @media (max-width: 768px) {
              padding: 0 20px;
            }

            &:first-child {
                border-right: 1px solid #fff;
            }
        }
    }

    &-text {
        text-align: center;
        color: #fff;
        font-family: "Montserrat", sans-serif;
        font-weight: 700;
        font-style: italic;
        font-size: 1rem;
        transform: translate(0px, 116px);
        @media (max-width: 768px) {
          transform: translate(0px, 68px);
          font-size: 14px;
        }
    }

}

.nav {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    transition: .3s all;
    z-index: 10;
    @media (max-width: 768px) {
      padding: 5px;
    }

    &.hidden{
        visibility: hidden;
        opacity: 0;
        transition: .3s all;
    }

    &-logo {
        &__link {
            padding-left: 15px;
            padding-top: 15px;
            display: block;
            @media (max-width: 768px) {
              padding: 0;
            }
            img {
                max-width: 120px;
                @media (max-width: 768px) {
                  max-height: 52px
                }
            }
        }
    }

    &-list {
        display: flex;
        align-items: center;
        padding-right: 9.66%;
        @media (max-width: 991px) {
          padding-right: 40px;
        }
        @media (max-width: 768px) {
          display: block;
          padding: 0 0 80px 0;
          position: relative;
          z-index: 5;
          &.nav-pc {
            display: none;
          }
        }

        &__item {
            margin-right: 34px;
            @media (max-width: 768px) {
              margin-right: 0;
              margin-bottom: 30px;
              &:last-child {
                margin-bottom: 0;
              }
            }

            &:last-child {
                margin-right: 0;
            }
        }

        &__link {
            font-size: 1.3rem;
            font-weight: bold;
            font-family: "Montserrat", sans-serif;
            color: #fff;
            text-decoration: none;
            position: relative;
            @media (max-width: 768px) {
              font-size: 20px;
              font-weight: 800;
              color: #141419;
            }

            &:after {
              position: absolute;
              left: 0;
              content: '';
              width: 100%;
              height: 2px;
              background: #fff;
              bottom: -1px;
              transform: scale(0, 1);
              transform-origin: center top;
              transition: transform 0.3s;
              bottom: -7px;
            }

            &:hover {
              &:after {
                transform: scale(1, 1); 
              }
            }
        }
    }
}

.fukuoka-parco {
    position: relative;
    z-index: 1;
    padding-top: 162px;
    display: none;
    @media (max-width: 991px) {
      // padding-top: 95px;
    }

    &__body {
      @media (max-width: 768px) {
        padding-left: 2px;
        padding-right: 2px;
        margin-bottom: 50px;
      }
    }

    &:before {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        background: #141419;
        opacity: 0.32;
        width: 100%;
        height: 100%;
    }

    &.animated {
      .fukuoka-parco__box-letter {
        // opacity: 1;
      }
      .fukuoka-parco__box{
         // animation: fadeInUp 1s ease-in-out forwards;
      }
    }

    &__img {
        position: absolute;
        width: 100%;
        top: 279px;
        opacity: 0.7;
        animation: slide-left 10s linear infinite;
        display: flex;
        align-items: center;
        width: 200%;

        img {
            width: 50%;
            height: 90px;
          @media (max-width: 768px) {
            height: 47px;
          }
        }
    }

    &__box {
        width: 555px;
        height: 100%;
        margin: auto;
        display: flex;
        align-items: center;
        position: relative;
        z-index: 2;
        padding-top: 100px;
        padding-block: 72px;
        transform: translateY(20px);
        @media (max-width: 768px) {
          width: 100%;
          height: auto;
          padding-bottom: 30px;
        }

        &:after {
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            // background: url('../images/letter.svg') no-repeat;
            background-size: 100%;
            pointer-events: none;
            @media (max-width: 768px) {
              display: none;
            }
        }

        &:before {
            position: absolute;
            content: "";
            width: 502px;
            height: 100%;
            left: 0;
            top: 2px;
            background: rgba(169, 151, 181, 0.25);
            backdrop-filter: blur(20px) brightness(1.4);
            border-left: 1px solid #939393;
            border-right: 1px solid #939393;
            border-top: 5px solid #141419;
            border-bottom: 32px solid #141419;
            right: 0;
            margin: auto;
            @media (max-width: 768px) {
              width: calc(100% - 45px);
              top: 0;
              height: 100%;
              pointer-events: none;
              // display: none;
            }
        }

        &-letter {
            position: absolute;
            top: -50px;
            left: 36%;
            right: 10px;
            /* margin: auto; */
            z-index: 9;
            transform: translate(190px, 0);
            opacity: 0;
            transition: opacity 2s ease-in-out;

            @media (max-width: 768px) {
              display: none;
            }
        }
    }

    &__text {
        font-size: 1.5rem;
        letter-spacing: 0.75px;
        line-height: 2;
        color: #fff;
        font-family: "Zen Kaku Gothic Antique", sans-serif;
        font-weight: bold;
        padding-left: 100px;
        padding-right: 100px;
        position: relative;
        text-align: center;
        @media (max-width: 768px) {
          padding: 0 22px;
          position: relative;
          .desc {
            position: relative;
            z-index: 2;
            padding: 0 28px 0px 28px;
          }
        }
      .icon-letter {
        @media (max-width: 768px) {
          display: flex;
          align-items: center;
          justify-content: center;
          position: absolute;
          top: -130px;
          left: 0;
          z-index: 2;
          width: 100%;
        }
      }
    }

    &__inv {
        position: relative;
        z-index: 1;
        text-align: right;
        margin-right: 120px;
        padding-top: 117px;
        padding-bottom: 67px;
        @media (max-width: 768px) {
          margin: 0;
          text-align: center;
          padding: 60px 0 20px 0;
        }
    }
  .icon-starfu {
    display: none;
    align-items: center;
    justify-content: center;
    position: absolute;
    @media (max-width: 768px) {
      display: flex;
    }
    &.starfu-1 {
      top: 0;
      left: -20px;
      @media (max-width: 768px) {
        display: none;
      }
    }
    &.starfu-2 {
      bottom: -20px;
      right: -20px;

      @media (max-width: 768px) {
        display: none;
      }
    }
  }
}

