* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.d-none {
  display: none;
}

.btn-menu-mb {
  @media (max-width: 768px) {
    display: block;
  }
  a {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.nav-link-menu {
  align-items: center;
  justify-content: center;
  text-align: center;
  position: fixed;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  transition: .5s all !important;
  z-index: 12;
  &:before {
    content: "";
    width: 100%;
    height: 100%;
    background: url("../images/bgmenu-mb.png") no-repeat center;
    background-size: cover;
    background-position: bottom;
    position: absolute;
    top: 0;
    filter: blur(8px);
    left: -1px;
  }

  &:after {
    content: "";
    width: 100%;
    height: 100%;
    background: url("../images/bgmenu-mb02.png") no-repeat center;
    background-size: cover;
    background-position: bottom;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0.8;
    z-index: 2;
  }
  @media (max-width: 768px) {
    display: flex;
  }
  &.active {
    left: 0;
  }
  .close-menu {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 12px;
    right: 12px;
    z-index: 3;
    a {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}

.mask-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 3s all !important;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: .4s all !important;
    &.show-pc {
      @media (max-width: 768px) {
        display: none;
      }
    }
    &.show-mb {
      @media (max-width: 768px) {
        display: block;
      }
    }
  }
}

