.head-main {
  position: relative;
  padding-top: 20px;
  padding-bottom: 20px;
  margin-bottom: -85px;
  z-index: 5;

  &:after {
    position: absolute;
    content: "";
    width: calc(100% - 110px);
    height: 85px;
    background: #000;
    left: 0;
    top: 0;

    @media (max-width: 768px) {
      display: none;
    }
  }

  &:before {
    position: absolute;
    content: "";
    width: 110px;
    height: 85px;
    background: url('../images/hear.svg') no-repeat;
    right: 0;
    top: 0;
    background-size: 102% 101%;

    @media (max-width: 768px) {
      display: none;
    }
  }

  &.head_active {
    &:after {
      position: fixed;
      max-width: 100%;
      top: 0;
      left: 0;
    }

    &:before {
      position: fixed;
      max-width: 100%;
      top: 0;
      right: 0;
    }

    &.RemoveActive {
      &:before {
        position: absolute;
      }

      &:after {
        position: absolute;
      }

      .header-title {
        opacity: 0;
        isibility: hidden;
      }
    }

    .header-title {
      position: fixed;
      top:21px;
    }
  }

  .header-title {
    font-size: 3rem;
    font-weight: bold;
    color: #fff;
    text-align: left;
    font-family: "Montserrat", sans-serif;
    position: relative;
    z-index: 5;
    transition: .1s all;

    @media (max-width: 768px) {
      display: none;
    }
  }
  
  @media (max-width: 768px) {
    display: none;
  }
  img {
    max-width: 100%;
  }
}

.head-main-mb {
  @media (max-width: 768px) {
